import { utils } from 'ethers'
import { Link } from 'react-router-dom'
import { useData } from '../dataContext'
import { useWeb3 } from '../web3'
import { connect } from '../web3/useProvider'

const Header = () => {
  const { myEtherBalance } = useData()
  const { account } = useWeb3()

  return (
    <div className="w-full">
      <div className="bg-purple">
        <div className="container text-white py-6 flex flex-col sm:flex-row sm:justify-between lg:items-center">
          <div className="text-xl mb-2 sm:mb-0">
            <Link to="/">DoomCatRescue</Link>
          </div>
          <div className="flex flex-col lg:flex-row gap-y-2 lg:gap-y-0 lg:gap-x-4 sm:text-right opacity-50 cursor-not-allowed">
            <div>AUCTIONS</div>
            <div className="hidden lg:block">|</div>
            <div to="/doomcats">DOOMCATS</div>
            <div className="hidden lg:block">|</div>
            {account
              ? <div>{account.substring(0, 8)}</div>
              : <button className="hover:underline mr-auto sm:mr-0 sm:ml-auto" onClick={() => connect()}>CONNECT</button>
            }
          </div>
        </div>
      </div>
      {account && <div className="container text-white text-sm pt-4 -mb-8 flex sm:justify-end sm:text-right">
        <div>
          <div>My balance</div>
          <div>{parseFloat(utils.formatEther(myEtherBalance)).toFixed(4)} ETH</div>
        </div>
      </div>}
    </div >
  )
}

export default Header
