import { useState, useEffect } from 'react'
import { BigNumber } from 'ethers'

function useTotalCats(doomCatRescue) {
  const [totalCats, setTotalCats] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue) return

    doomCatRescue.totalCats()
      .then(setTotalCats)
      .catch(console.error)
  }, [doomCatRescue])

  return totalCats
}

function useRemainingCats(doomCatRescue) {
  const [remainingCats, setRemainingCats] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue) return

    doomCatRescue.remainingCats()
      .then(setRemainingCats)
      .catch(console.error)

    function updateRemainingCats() {
      setRemainingCats(remainingCats => remainingCats.sub(1))
    }

    doomCatRescue.on('Rescue', updateRemainingCats)

    return () => {
      doomCatRescue.removeListener('Rescue', updateRemainingCats)
    }
  }, [doomCatRescue])

  return remainingCats
}

function useRescuePriceInitial(doomCatRescue) {
  const [rescuePriceInitial, setRescuePriceInitial] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue) return

    doomCatRescue.rescuePriceInitial()
      .then(setRescuePriceInitial)
      .catch(console.error)
  }, [doomCatRescue])

  return rescuePriceInitial
}

function useRescuePrice(doomCatRescue) {
  const [rescuePrice, setRescuePrice] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue) return

    function updateRescuePrice() {
      doomCatRescue.rescuePrice()
        .then(setRescuePrice)
        .catch(console.error)
    }

    updateRescuePrice()

    doomCatRescue.on('Rescue', updateRescuePrice)

    return () => {
      doomCatRescue.removeListener('Rescue', updateRescuePrice)
    }
  }, [doomCatRescue])

  return rescuePrice
}

function useRescuePriceFinal(doomCatRescue, totalCats, genesisCats, rescuePriceInitial) {
  const [rescuePriceFinal, setRescuePriceFinal] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue || !totalCats || !genesisCats || !rescuePriceInitial) return

    Promise.all([
      doomCatRescue.rescuePriceIncrement(),
      doomCatRescue.rescueTrancheSize(),
    ]).then(([rescuePriceIncrement, rescueTrancheSize]) => {
      const final = rescuePriceInitial.add(totalCats.sub(genesisCats).div(rescueTrancheSize).mul(rescuePriceIncrement))
      setRescuePriceFinal(final)
    }).catch(console.error)

  }, [doomCatRescue, totalCats, genesisCats, rescuePriceInitial])

  return rescuePriceFinal
}

export {
  useTotalCats,
  useRemainingCats,
  useRescuePriceInitial,
  useRescuePrice,
  useRescuePriceFinal,
}
